import React, { Component } from 'react'
import spinner from '../images/spinner.gif'

class Spinner extends Component {
  render() {
    let hidden = 'hidden'
    let loading = this.props.loading
    if (loading === true) {
      hidden = 'visible'
    }
    return (
      <img
        className="spinner"
        style={{
          visibility: hidden,
          width: '32px',
          position: 'absolute',
          zIndex: '11',
          display: 'table',
          margin: '2px',
        }}
        style22={{ width: '32px' }}
        src="data:image/svg+xml;base64,"
        alt="spinner"
      />
    )
  }

  render_old() {
    return (
      <div style={{ textAlign: 'center' }}>
        <img style={{ width: '32px' }} src={spinner} alt="spinner" />
      </div>
    )
  }
}

export default Spinner
